<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">
      <router-link class='el-button--text' to="/page/list" replace>页面管理</router-link>
      / {{pageTitle}}
    </el-row>
    <el-row type="flex" justify="start" style="margin-top: 30px;">
     <el-form ref="form" :model="form" :rules="rules" label-width="100px" label-suffix=":" label-position="left" style="width: 100%">
        <el-form-item label="页面名称" prop="name">
          <el-input v-model="form.name" placeholder="请输入页面名称" />
        </el-form-item>
        <h1 style="font-size: 20px">页面内容</h1>
        <template v-for="(item, index) in form.page_items">
          <el-form-item v-if="!item._destroy" :label="item.item_type == 'content' ? '文字' : '图片'" :key="index">
            <el-row>
              <el-input class="content" v-if="item.item_type == 'content'" v-model="item.content" placeholder="请输入文字" type="textarea" />
              <template v-else>
                <div v-if="item.image.url" class="avatar">
                  <img :src="'https://szebra.cn' + item.image.url" />
                  <i class="el-icon-circle-close" @click="removeImage(index)" />
                </div>
                <el-upload v-else class="avatar-uploader"
                          action="https://szebra.cn/api/admin/images"
                          :show-file-list="false"
                          :on-success="handleAvatarSuccess(index)"
                          name="image[file]"
                          :before-upload="beforeAvatarUpload">
                  <i class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </template>
              <el-button class="delete-btn" icon="el-icon-delete" size="small" @click="deletePage(index)" :key="'btn' + index" type="danger" />
            </el-row>
            <el-row v-if="item.item_type != 'content'" style="margin-top: 20px">
              公众号文章地址: <el-input v-model="item.link" />
            </el-row>
          </el-form-item>
        </template>
        <el-row style="margin-bottom: 30px;margin-top: 30px;">
          <el-button type="primary" plain size="small" @click="addText">添加文字</el-button>
          <el-button type="primary" plain size="small" @click="addImage">添加图片</el-button>
        </el-row>
        <el-button type="primary" @click="onSubmit">{{submitBtnTitle}}</el-button>
      </el-form>
    </el-row>
  </div>
</template>
<script>
const G_IMAGE_SIZE = 2 // 图片大小

export default {
  components: {
  },
  data () {
    return {
      pageState: '', // 1新增，2编辑，3预览
      pageTitle: '', // 页面标题
      pageId: '', // 自定义页面id
      submitBtnTitle: '',
      form: {
        id: '',
        name: '',
        page_items: []
      },
      rules: { // 校验规则
        name: { required: true, message: '请输入资讯标题名称', trigger: 'blur' }
      }
    }
  },
  methods: {
    validateItems () {
      for (let item of this.form.page_items) {
        if (item._destroy) continue
        switch (item.item_type) {
          case 'content':
            if (!item.content) {
              this.$message.error('文本内容不可为空')
              return
            }
            break
          case 'image':
            if (!item.image || !item.image.url || !item.image.id) {
              this.$message.error('图片不可为空')
              return
            }
        }
      }
      return true
    },
    formatSubmitValue () {
      if (!this.validateItems()) return

      const ret = { id: this.form.id, page: Object.assign({}, this.form) }
      ret.page.page_items = ret.page.id = undefined
      ret.page.page_items_attributes = this.form.page_items.map(item => {
        item = Object.assign({}, item)
        if (item.item_type === 'image') {
          item.image_attributes = Object.assign(item.image)
          item.image = undefined
        }
        return item
      })
      return ret
    },
    deletePage (index) {
      this.form.page_items[index]._destroy = true
      this.form.page_items.splice(index, this.form.page_items[index].id ? 0 : 1)
    },
    addText () {
      this.form.page_items.push({
        item_type: 'content',
        content: '',
        position: this.form.page_items.length
      })
    },
    addImage () {
      this.form.page_items.push({
        item_type: 'image',
        image: { id: '', url: '' },
        position: this.form.page_items.length
      })
    },
    submit () {
      const data = this.formatSubmitValue()
      console.log(JSON.stringify(data))
      if (!data) return
      this.$http[this.submitFuncName](data).then(res => {
        if (res.data.retcode == 0) {
          this.$message.success('保存成功')
          Object.assign(this.form, res.data.result_rows)
          this.$router.push({ path: '/page/list'})
        } else {
          this.$message.error('保存失败');
        }
      })
    },
    onSubmit () {
      this.$refs.form.validate()
      .then(valid => this.submit())
      .catch(() => {})
    },
    deleteTextImage () {},
    removeImage (index) {
      this.form.page_items[index].image.url = ''
    },
    handleAvatarSuccess (index) {
      return data => this.form.page_items[index].image = data.result_rows
    },
    beforeAvatarUpload(file) {
      const isImg = file.type.startsWith('image/');
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isImg) {
        this.$message.error('只允许上传图片');
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!');
      }
      return isImg && isLt2M;
    }
  },
  created () {
    switch (this.$router.currentRoute.name) {
        case 'page_create':
          this.pageTitle = '添加页面'
          this.submitBtnTitle = '提交'
          this.submitFuncName = 'pageCreate'
          break
        default:
          this.pageTitle = '编辑页面'
          this.submitBtnTitle = '保存所有编辑结果'
          this.submitFuncName = 'pageUpdate'
          this.$http.pageDetail(this.$router.currentRoute.params)
            .then(res => {
              if (!res) return
              Object.assign(this.form, res.data.result_rows)
            })
    }
  }
}
</script>

<style>
  .el-input {
    width: 270px;
  }
  .content {
    width: 80%;
    float: left;
  }
  .delete-btn {
    margin-left: 10px;
  }
  .avatar-uploader {
    max-width: 80%;
    float: left;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    display: inline-block;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    position: relative;
    float: left;
    margin-right: 5px;
  }
  .avatar img {
    margin-top: 6px;
    margin-right: 6px;
    width: 178px;
    height: 178px;
  }
  .avatar i {
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0.6;
  }
  .avatar i:hover {
    opacity: 1;
    cursor: pointer;
  }
</style>
